const buttons = document.querySelectorAll(".filters button");
const typeButtons = document.querySelectorAll(".filters .type button");
const logicButtons = document.querySelectorAll(".filters .logic button");
const logosSection = document.querySelector("section.logos");

typeButtons.forEach(function (btn) {
  btn.addEventListener("click", function () {
    filterType(this);
  });
});

logicButtons.forEach(function (btn) {
  btn.addEventListener("click", function () {
    filterLogic(this);
  });
});

function filterType(btn) {
  const logosDiv = document.querySelectorAll("section.logos div");

  const logos = document.querySelectorAll(".logos a");
  const attrId = btn.getAttribute("id");
  activateBtn(btn, 0);
  changeSortOrder(btn);
  flipCard(attrId);
  logicButtons.forEach(function (logicBtn) {
    if (logicBtn.classList.contains("active")) {
      console.log(logicBtn)
      if (logicBtn.getAttribute('id') == "alphabet") {
        logicBtn.classList.contains("up")
          ? alphaOrder(logosDiv, "up")
          : alphaOrder(logosDiv, "down");
      } else {
        logicBtn.classList.contains("up")
          ? dateOrder(logosDiv, "up")
          : dateOrder(logosDiv, "down");
      }
    }
  });

  logos.forEach(function (logo) {
    if (logo.getAttribute("data-tags").indexOf(attrId) === -1) {
      logo.classList.add("flipped");
    }
  });
}

function flipCard(id) {
  if (id == "Repeat") {
    logosSection.classList.add("is-flipped");
  } else {
    logosSection.classList.remove("is-flipped");
  }
}

function filterLogic(btn) {
  const logosDiv = document.querySelectorAll("section.logos div");
  const attrId = btn.getAttribute("id");
  activateBtn(btn, 1);
  removeOrder(logosDiv);
  const sortOrder = changeSortOrder(btn);

  if (attrId == "alphabet") {
    alphaOrder(logosDiv, sortOrder);
  } else if (attrId == "date") {
    dateOrder(logosDiv, sortOrder);
  }
}

function changeSortOrder(btn) {
  let sortOrder = "up";
  if (btn.classList.contains("up")) {
    sortOrder = "down";
  }

  resetOrder(btn);

  // Add new sortOrder
  btn.classList.add(sortOrder);
  return sortOrder;
}

function resetOrder(btn) {
  // Remove sortOrder class
  btn.classList.remove("down");
  btn.classList.remove("up");
}

function alphaOrder(logosDiv, sortOrder) {
  console.log('alpha order')
  const tabLogos = [];
  logosDiv.forEach(function (div) {
    let txtLogo = "";
    if (logosSection.classList.contains("is-flipped")) {
      txtLogo = div.children[1].children[0].getAttribute("alt");
    } else {
      txtLogo = div.children[0].children[0].getAttribute("alt");
    }
    tabLogos.push(txtLogo);
  });
  sortTab(tabLogos, sortOrder);

  logosDiv.forEach(function (div) {
    let txtLogo = "";
    if (logosSection.classList.contains("is-flipped")) {
      txtLogo = div.children[1].children[0].getAttribute("alt");
    } else {
      txtLogo = div.children[0].children[0].getAttribute("alt");
    }
    setOrder(div, tabLogos, txtLogo);
  });
}

function dateOrder(logosDiv, sortOrder) {
  const tabLogos = [];
  logosDiv.forEach(function (div) {
    let txtLogo = "";
    if (logosSection.classList.contains("is-flipped")) {
      txtLogo = div.children[1].getAttribute("data-date");
    } else {
      txtLogo = div.children[0].getAttribute("data-date");
    }
    tabLogos.push(txtLogo);
  });
  sortTab(tabLogos, sortOrder);

  logosDiv.forEach(function (div) {
    let txtLogo = "";
    if (logosSection.classList.contains("is-flipped")) {
      txtLogo = div.children[1].getAttribute("data-date");
    } else {
      txtLogo = div.children[0].getAttribute("data-date");
    }
    setOrder(div, tabLogos, txtLogo);
  });
}

function sortTab(tab, sortOrder) {
  tab.sort();
  if (sortOrder == "down") tab.reverse();
}

function setOrder(div, tabLogos, txtLogo) {
  tabLogos.forEach(function (tabLogoTxt, i) {
    if (txtLogo == tabLogoTxt) {
      div.style.order = i;
    }
  });
}

function activateBtn(btn, status) {
  const active = "active";

  // Type button
  if (status == 0) {
    typeButtons.forEach(function (b) {
      b.classList.remove(active);
    });
  }

  // Logic button
  else {
    logicButtons.forEach(function (b) {
      b.classList.remove(active);
      if (b.getAttribute("id") != btn.getAttribute("id")) {
        resetOrder(b);
      }
    });
  }

  btn.classList.add(active);
}

function removeOrder(logosDiv) {
  logosDiv.forEach(function (div) {
    div.style.order = 0;
  });
}
